import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';

var hamburger = {
    selector: '.hamburger',
    init: function() {
        var self = this,
            $hamburger = $( self.selector ),
            $navigation = $( '.navigation' );

        var navigation = document.querySelector( '.navigation__container' );

        $hamburger.on( 'click', function( e ) {
            $navigation.addClass( 'navigation--animate' );
            $( document.body ).toggleClass( 'navigation-opened' );

            if ( $( document.body ).hasClass( 'navigation-opened' ) ) {
                disableBodyScroll( navigation, { reserveScrollBarGap: true } );
                var scrollBarGap = window.innerWidth - document.documentElement.clientWidth;

                if ( scrollBarGap > 0 ) {
                    $( '.fl-symbol--hamburger' ).css( 'transform', 'translate3d(-' + scrollBarGap + 'px, 0, 0)' );
                }
            } else {
                enableBodyScroll( navigation );
                $( '.fl-symbol--hamburger' ).removeAttr( 'style' );
            }
        } );
    }
}

export {
    hamburger
};
