var smoothScroll = {
    selector: 'a[href*="#"]',
    init: function() {
        var self = this,
        scroll = new SmoothScroll( self.selector, {
            ignore: '[data-scroll-ignore]',
            topOnEmptyHash: false,
            speed: 1000,
            offset: 0,
            updateURL: false,
            easing: 'easeInOutCubic',
        } );
    }
}

export {
    smoothScroll
};
